import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Parallax } from 'swiper';

import React, { useEffect } from 'react';
import { $, bgWebp } from 'components/';
import webp from 'img/Home/infoBlock/child.webp';
import jpg from 'img/Home/infoBlock/child.jpg';

import './Style/Slider.scss';
import 'swiper/css';
import 'swiper/css/navigation';

export default function SliderGroup() {
  useEffect(() => {
    bgWebp({ webp, jpg, where: '.parallax-bg' });
  }, []);

  const handlerSize = () => {
    const container =
      (+document.documentElement.offsetWidth -
        $('.container').elem.offsetWidth) /
      2;

    $('.swiper-button-prev').style('width', container);
    $('.swiper-button-next').style('width', container);

    if (document.documentElement.clientWidth > 756) {
      $('.mySwiper .swiper-slide').style('paddingLeft', container);
      $('.mySwiper .swiper-slide').style('paddingRight', container);
    } else {
      $('.mySwiper .swiper-slide').style('paddingLeft', '');
      $('.mySwiper .swiper-slide').style('paddingRight', '');
    }
  };

  useEffect(() => {
    handlerSize();
    window.addEventListener('resize', handlerSize);
    window.addEventListener('click', handlerSize);
    return function cleanup() {
      window.removeEventListener('resize', handlerSize);
      window.removeEventListener('click', handlerSize);
    };
  }, []);

  return (
    <>
      <Swiper
        speed={600}
        parallax={true}
        modules={[Parallax, Navigation]}
        className='mySwiper'
        navigation={{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        }}
      >
        <div
          slot='container-start'
          className='parallax-bg'
          data-swiper-parallax='-23%'
        ></div>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Kids workout</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Group</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className='title' data-swiper-parallax='-300'>
            <h1>Group</h1>
          </div>
          <div className='subtitle' data-swiper-parallax='-200'>
            <h3>Program</h3>
          </div>
          <div className='text' data-swiper-parallax='-100'>
            <p className='mini'>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
              dictum mattis velit, sit amet faucibus felis iaculis nec.
            </p>
          </div>
        </SwiperSlide>
        <div className='swiper-button-prev'></div>
        <div className='swiper-button-next'></div>
      </Swiper>
    </>
  );
}
