import { bgWebp } from 'components/';

import webp from 'img/Home/BannerHead.webp';
import jpg from 'img/Home/BannerHead.jpg';
import { useEffect } from 'react';

import './style/Banner.scss';

export const Banner = () => {
  useEffect(() => {
    bgWebp({ webp, jpg, where: '.banner__img', rmClass: '.banner' });
  });

  return (
    <section className='banner load'>
      <div className='banner__img'>
        <div className='banner__load' />
      </div>

      <div className='banner__ground '>
        <div className='container'>
          <div className='banner__menu'>
            <div className='banner__body'>
              <h2 translate='no' className='banner__title'>
                <span>Train</span> <span>hard and</span>
                <span>get results</span>
              </h2>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
